<template>
  <div>
    <div class="seBox">
      <el-input
        v-model="searchInput"
        class="el1"
        placeholder="請輸入關鍵字"
        prefix-icon="el-icon-search"
      />
    </div>

    <div class="mv_wrap">
      <div
        v-for="(item, index) in mvList"
        :key="index"
        class="mv_item"
      >
        <img
          class="poster"
          :src="`https://image.tmdb.org/t/p/w1280/${item.poster_path}`"
        >
      </div>
    </div>
  </div>
</template>

<script>
import { apiMovieList } from '../api/index.js';

export default {
  data () {
    return {
      searchInput: '',
      allData: [],
      mvList: []
    };
  },
  mounted () {
    this.getDefaultData();
  },
  methods: {
    getDefaultData () {
      apiMovieList()
        .then(res => {
          console.log(res);
          this.allData = res.data;
          this.mvList = res.data.results;
        }).catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style lang="scss">

  .seBox {
    display: flex;
    align-items: center;

    span {
      margin-right: 10px;
      font-size: 18px;
    }
  }

  .el1 {
    width: 300px;

    input {
      border-radius: 0;
      border: none;
      border-bottom: 1px solid #d1cece;
    }
  }

  .mv_wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 35px;
  }

  .mv_item {
    padding-bottom: 40px;
  }

  .poster {
    width: 250px;
    height: 357px;
  }

</style>
